<template lang="pug">
  div
    .hr
    .actions-board    
      a.main-button(href="#" @click.prevent="mainButtonClickHandler") {{ buttonName }}
      a.main-button(href="#" @click.prevent="dontBuy" v-if="cancelBuy") Не покупать
      a.main-button(href="#" @click.prevent="payFine" v-if="checkJail") Штраф 50М
      a.main-button(href="#" @click.prevent="openSwap" v-if="!statusGo") Обмен
      a.main-button(href="#" @click.prevent="checkWinner") X
</template>
<script>
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
export default {
  name: 'Actions',
  computed: {
    ...mapGetters(['currentCard','currentPlayer', 'checkJail']),
    ...mapState({
      statusGo: (state)=>state.game._endOfTurn,
      statusAction: (state)=>state.game._action,
      winner: (state)=>state.game.winner,
    }),
    cancelBuy(){
      return this.currentCard.canBuy && !this.currentCard.owner && this.statusAction
    },
    buttonName(){
      if(this.statusGo){
        return "Бросить кубики";
      }
      if(this.statusAction){
        if(this.cancelBuy){
          return "Купить"
        } else if(this.currentCard.owner) {
          return "Заплатить";
        } else {
          return "Выполнить действие";
        }
      }
      if(!this.statusAction && !this.statusGo){
        return "Закончить ход"
      }
    },    
  },
  methods:{    
    ...mapActions([
      'startTurn', 
      'doAction', 
      'endTurn',
      'dontBuy',
      'gameOver',
      'payFine',
      ]),
    ...mapMutations([
      'win',
      'openSwap'
    ]),
    checkWinner(){
      this.gameOver(this.currentPlayer.id);
      if(this.winner){
        this.win(this.winner);
      }
    },
    mainButtonClickHandler(){
      if(this.statusAction){
        this.doAction();
      } else if(!this.statusGo) {
        this.endTurn();
      } else {
        this.startTurn();
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .actions-board{
    font-size: 18pt;
    margin: 5px 0 0 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    a{
      flex: 1 0 auto;
      text-decoration: none; 
      color: #300;      
    }
  }
</style>