<template lang="pug">
  section.options-board
    p Опции
    .hr
    div
      input#sale(type="checkbox" v-model="saleOnGameoverCheckbox")
      label(for="sale") Выставить собственность на продажу, если игрок сдался.
    div
      input#log(type="checkbox" v-model="viewLogCheckbox")
      label(for="log") Выводить лог игры в консоль браузера.
    p(@click="saveAndExit") Выход
    .hr
    p.info Перед вами игра Монополия в тематике Гарри Поттера. 
      | Игра локальная, на одном устройстве.
      | Игра создана для того, что бы вечером лежа со своей второй половинкой, 
      | вы могли сыграть в монополию и не покупать для этого настольную игру.
      | Игра находится в стадии alpha - очень много ошибок и багов, надеюсь вы поможете мне их найти.
      | Все предложения и заамечания по ошибкам слать на 
      a(href="mailto:pomatsuev@gmail.com") pomatsuev(собака)gmail.com
    p.info Если нажимать на карту, будут происходить стандартные действия по порядку,
      | Сначало вы бросите кубики и перейдете на карту, потом в зависимости от карты, вы заплатите ренту
      | или купите собственоность, далее вы передадите ход. Остальные функции делаются на соответствующие ссылки.
      | Понимаю что интерфейс и анимации не космос, я все же не художник... Если у вас есть предложения по оформлению,
      | буду рад сотрудничеству.
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
export default {
  name: 'OptionsBoard',
  computed: {
    ...mapState(['saleOnGameOver', 'viewLog']),
    saleOnGameoverCheckbox: {
      get() {
        return this.saleOnGameOver;
      },
      set(value){
        this.switchSaleOnGameOver(value);
      }
    },
    viewLogCheckbox: {
      get(){
        return this.viewLog;
      },
      set(value) {
        this.switchViewLog(value);
      }
    }
  },
  methods:{
    ...mapMutations(['switchSaleOnGameOver','closeOptions', 'switchViewLog']),
    ...mapActions(['saveOptionsToStorage']),
    saveAndExit(){
      this.saveOptionsToStorage();
      this.closeOptions();
    }
  }
}
</script>

<style lang="scss" scoped>
  .options-board{
    font-size: 16pt;
    text-align: center;
    .info {
      font-size: 20pt;
      padding: 0 10px;
    }
    a{
      display: inline;
    }    
  }
</style>