<template lang="pug">
  .pennant(@click="penantOpen" :class="penantClass" v-click-outside="fn")
    transition(name="anim", @enter="enter" mode="out-in")   
      .penant-body(v-if="!isOpen")
        .cube1(:class="{ grey: statusGo }") {{cube1}}
        .cube2(:class="{ grey: statusGo }") {{cube2}}
      .penant-body(v-else)
        .player {{ player.name }}
        .cubes Выпало: {{ cube1 }} и {{ cube2 }}
</template>
<script>
import { mapActions, mapState } from 'vuex';
export default {
  name: 'Pennant',
  data(){
    return {
      isOpen: false,
    }
  }, 
  computed: {
    ...mapState({
      statusGo:(state)=> state.game._endOfTurn,
      cube1:(state)=> state.game.cubes.cube1,
      cube2:(state)=> state.game.cubes.cube2,
      player:(state)=> state.game.cubes.player,
    }),
    penantClass(){
      return { 'open': this.isOpen, 'closed': !this.isOpen }
    }
  },
  methods: {
    enter(){
      console.log('fdafdaf');
    },
    fn(){
      this.isOpen = false;      
    },
    penantOpen(){            
      this.isOpen = !this.isOpen;      
    }
  }, 
}
</script>
<style lang="scss" scoped>
  @mixin cube($top){
    font-size: 20pt;
    width: 20px;
    position: absolute;
    right: 22px;
    top: $top + px;   
  }
  .pennant{
    position: absolute;
    background: url('../assets/img/pennant.png');
    display: block;
    width: 200px;
    height: 122px;
    top: 180px;    
    z-index: 9999;
    .penant-body{
      position: relative;
      height: 100%;      
      color: white;
      text-align: center;
      .player{
        font-size: 14pt;
        width: 100%;
        position: absolute;
        top: 24px;
        transform: rotate(-10deg);        
      }
      .cubes{
        font-size: 14pt;
        width: 100%;
        position: absolute;
        top: 76px;
        transform: rotate(-10deg);        
      }
      .grey{
        color: #ffffff2c;        
      }
      .cube1{
        @include cube(12);               
      }
      .cube2{
        @include cube(66);         
      }
    }       
    &.open{
      left: 0px;
      transition: 1s;
    }
    &.closed{
      left: -155px;
      transition: 1s;
    }
  }

</style>