<template lang="pug">
  div.player-cards(:class="openClass", v-click-outside="outsideClickHandler") 
    .header(@click="switchOpen") Карты
    div(v-if="player")
      a(href="#" @click.prevent="changePlayer(-1)") Назад 
      | / {{ player.name }} / 
      a(href="#" @click.prevent="changePlayer(1)") Вперед
    cards-list.list(v-if="player" :cards="cards" :actionEnable="isActionEnable" @click="selectCard($event)")
</template>

<script>
import { mapGetters, mapMutations, mapActions, mapState } from 'vuex'
import CardsList from './CardsList'

export default {
  name: 'PlayerCards',
  props: {
    allNoEdit:{
      type: Boolean
    },
    auction:{
      type: Boolean
    }
  },
  data(){    
    return {
      isOpen: false,
      player: null,     
    }
  },
  mounted(){
    this.player = this.currentPlayer;    
  },
  components: {
    'cards-list': CardsList,
  },
  computed: {
    ...mapState(['game']),
    ...mapGetters(['playerCards', 'selectedCard', 'currentPlayer']),
    openClass(){
      return { 'open': this.isOpen }
    },
    cards(){      
      return this.getCards(this.player);
    },
    isActionEnable(){    
      return !this.allNoEdit && this.player.id === this.currentPlayer.id;
    }
  },
  methods: {
    ...mapMutations(['selectCard']),    
    switchOpen(){
      this.isOpen = !this.isOpen;
      if(this.isOpen){
        this.player = this.currentPlayer;
      }
    },
    outsideClickHandler(){
      this.isOpen = false;
    },
    getCards(player){ 
      return this.game.playerCards(player.id);
    },
    changePlayer(index){
      const playersArray = this.game.players;            
      let nextIndex = this.player.index + index
      if(nextIndex > playersArray.length-1){
        nextIndex = 0;
      } else if(nextIndex < 0){
        nextIndex = playersArray.length-1;
      }
      this.player = playersArray[nextIndex];
    }
  }
}
</script>

<style lang="scss" scoped>
  .player-cards{
    width: 100%;
    //height: 185px;
    position: fixed;
    bottom: -160px;
    //bottom: -130px;
    z-index: 9999;
    background: #d9ba6f;
    font-size: 18pt;
    transition: 0.5s;
    .list{
      padding-top: 10px;
    }    
    &.open{
      bottom: 0;
      transition: 0.5s;
    }
  }
</style>