const names = [
  'Гарри Поттер',
  'Рон Уизли',
  'Волан-де-Морт',
  'Драко Малфой',
  'Северус Снегг',
  'Альбус Дамблдор',
  'Рубеус Хагрид',
  'Полумна Лавгуд',
  'Добби',
  'Невилл Долгопупс',
  'Минерва Макгонагалл',
  'Белатриса Лестрейндж',
  'Римус Люпин',
  'Геллерт Гриндевальд',
  'Гермиона Грейнджер'
];

function shuffle(array){
  for(let i = array.length -1; i > 0; i--){
    let j = Math.floor(Math.random() * (i+1));
    [array[i], array[j]] = [array[j], array[i]];
  }
}

export default (count) => {
  shuffle(names);
  return names.slice(count-1);
}
 