<template lang="pug">
  section.swap-board
    ul(v-if="!tradePlayer")
      div Выберите игрока с кем будет проходить обмен:
      li(v-for="player in players" :key="player.id") 
        a(@click.prevent="selectPlayer(player)" href="#") {{ player.name }}
    div(v-else)
      div {{ tradePlayer.name }} / {{ tradePlayer.cash }}
      div Деньги: {{ tradePlayerCash }} 
        a.link(href="#" @click.prevent="inc('tradePlayer', 10)" v-if="!tradePlayerAccept") +10
        a.link(href="#" @click.prevent="inc('tradePlayer', 50)" v-if="!tradePlayerAccept") +50
        a.link(href="#" @click.prevent="inc('tradePlayer', -10)" v-if="!tradePlayerAccept") -10
        a.link(href="#" @click.prevent="inc('tradePlayer', -50)" v-if="!tradePlayerAccept") -50
        a.link(href="#" @click.prevent="accept('tradePlayer')") {{ tradePlayerAccept ? 'Отмена' : 'Принять' }}           
        card-list(:cards="tradePlayerCards", :actionEnab="true", group="tradePlayer",  :border="true", v-show="!tradePlayerAccept" :drag="true")      
        card-list.top2.bg(:cards="tradePlayerCardsSelected", :actionEnab="true", group="tradePlayer", :border="true" :drag="true" :style={ height: '60px' })
      div /ОБМЕН/      
        card-list.bg(:cards="currentPlayerCardsSelected", :actionEnab="true", group="currentPlayer", :border="true" :drag="true" :style={ height: '60px' })     
        card-list.top2(:cards="currentPlayerCards",  :actionEnab="true", group="currentPlayer", :border="true" v-show="!currentPlayerAccept" :drag="true")
      div Деньги: {{ currentPlayerCash }}  
        a.link(href="#" @click.prevent="inc('currentPlayer', 10)" v-if="!currentPlayerAccept") +10
        a.link(href="#" @click.prevent="inc('currentPlayer', 50)" v-if="!currentPlayerAccept") +50
        a.link(href="#" @click.prevent="inc('currentPlayer', -10)" v-if="!currentPlayerAccept") -10
        a.link(href="#" @click.prevent="inc('currentPlayer', -50)" v-if="!currentPlayerAccept") -50      
        a.link(href="#" @click="accept('currentPlayer')") {{ currentPlayerAccept ? 'Отмена' : 'Принять' }}
      div {{ currentPlayer.name }} / {{ currentPlayer.cash }}
    a.link(href="#" @click.prevent="closeSwap") ВЫХОД   
</template>

<script>
import { mapMutations, mapState, mapGetters, mapActions } from 'vuex';
import draggable from 'vuedraggable';
import CardsList from './CardsList';

export default {
  name: 'SwapBoard',
  components:{
    draggable,
    'card-list': CardsList,
  },
  data(){
    return{
      tradePlayer: null,
      tradePlayerCards: [],
      tradePlayerCardsSelected: [],
      tradePlayerCash: 0,
      tradePlayerAccept: false,
      currentPlayerCards: [],
      currentPlayerCardsSelected: [],
      currentPlayerCash: 0,
      currentPlayerAccept: false
    }
  },
  computed:{
    ...mapState(['game']),
    ...mapGetters(['currentPlayer']),
    players(){
      return this.game.players.filter(plr=>plr.id!==this.game.whoGo&&!plr.gameOver);
    }
  },
  methods: {
    ...mapMutations(['closeSwap']),
    ...mapActions(['swapCard']),
    selectPlayer(player){
      this.tradePlayer = player;
      this.tradePlayerCards = this.game.board.filter(card=>card.owner===player.id);  
      this.currentPlayerCards = this.game.board.filter(card=>card.owner===this.currentPlayer.id);    
    },
    inc(prefix, cash){
      if(this[prefix+'Cash']+cash < 0){
        return;
      }      
      this[prefix+'Cash'] += cash;
    },
    accept(prefix) { 
      console.log(this.tradePlayerCardsSelected); 
      console.log(this.currentPlayerCardsSelected);  
      if(this[prefix+'Cash']>this[prefix].cash){
        return;
      }
      this[prefix+'Accept'] = !this[prefix+'Accept'];
      if(this.tradePlayerAccept && this.currentPlayerAccept){
        const result = {};
        result.saler = { player: this.tradePlayer, cards: this.tradePlayerCardsSelected, cash: this.tradePlayerCash };
        result.customer = { player: this.currentPlayer, cards: this.currentPlayerCardsSelected, cash: this.currentPlayerCash };
        this.swapCard(result);
        this.closeSwap();
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .swap-board {
    margin-top: 10px;
    text-align: center;
    font-size: 16pt;
    a{
      text-decoration: none;
    }
    .link{
      margin: 0 3px;
    }
  }    
  .bg{    
    background: lightgray;
  }
  .top2{
    margin-top:2px;
  } 
  input{
    margin: 5px 0;
  }
</style>