<template lang="pug">
  section.start-screen
    img.logo(src="../assets/img/hogwarts-logo-png-1.png")
    p Монополия Гарри Поттер       
    p Новая игра
    div(@click='startGame(2)') (2 игрока)
    div(@click='startGame(3)') (3 игрока)
    div(@click='startGame(4)') (4 игрока)
    div(@click="loadGameHandle") Загрузить игру
    p(@click='openOptions') Настройки
    p(v-if="winner") В прошлой игре победил: {{ winner.name }}
</template>
<script>
import { mapMutations, mapState } from 'vuex'
export default {
  name: 'StartScreen',
  methods: {
    ...mapMutations(['startGame', 'openOptions', 'loadGame']),
    loadGameHandle(){
      this.loadGame();
    }
  },
  computed: {
    ...mapState(['winner'])
  }
}
</script>
<style lang="scss" scoped>
  .start-screen{
    text-align: center;
    font-size: 18pt;
    p{
      margin: 10px 0;
    }
    .logo{
      padding: 30px 0 0 0;
      max-width: 250px;
    }
  }
</style>