<template lang="pug">
  .player-info
    .hr
    player-cards
    ul.player-list
      li(v-for="player in game.players" :key="player.id" :class="activeElementStyle(player)") {{ player.name }} / Деньги: {{ player.cash }}М
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import PlayerCards from './PlayerCards';

export default {
  name: 'PlayerInfo',
  components:{
    'player-cards': PlayerCards,
  },
  computed: {
    ...mapState(['game']),
    ...mapGetters(['currentPlayer'])
  },
  methods: {    
    activeElementStyle(player){
      let resultClass = {'game-over': player.gameOver};
      if(player.id === this.currentPlayer.id){
        Object.assign(resultClass, {'active': true, ['player'+player.index]: 'true'})
      }
      let toJail = false;
      for(let i = 0; i<this.game.jail.length; i++){
        if(this.game.jail[i].id === player.id){
          toJail = true;
          break;
        };
      }
      Object.assign(resultClass, { 'into-jail': toJail });
      return resultClass;
    }
  }
}
</script>

<style lang="scss" scoped>  
  .player-info{
    //margin-top: 10px;
    font-size: 14pt;
  }
  .player-list{
    width: 300px;
    padding: 0;
    margin: 0 auto;    
    list-style-type: none;
    color: #30030054;
    .game-over{
      text-decoration: line-through;
    }
    @each $number, $color in (0, #ff4848), (1, #3939ff), (2, #92dd92), (3, #ffa500){
      li.active.player#{$number}{
        color: #300300;
        &::before{
          content: '';
          margin: 0px 5px 0 -20px; 
          color: $color;      
          display: inline-block;
          height: 6px;
          width: 6px;
          border-radius: 50%;
          border: 1px solid #300300;          
          background: $color;
        }
      }
      li.into-jail{
        &::after{
          content: '#';
          margin: 0 0 0 5px;
          font-weight: 700;
        }
      }
    }    
  }  
  
</style>