<template lang="pug">  
  div
    transition(name="change-screen" mode="out-in")
      component(:is="currentComponent")    
</template>

<script>
import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
import StartScreen from './components/StartScreen';
import GameBoard from './components/GameBoard';
import AuctionBoard from './components/AuctionBoard';
import CardOptionsBoard from './components/CardOptionsBoard';
import SwapBoard from './components/SwapBoard';
import Screens from './Screens';
import OptionsBoard from './components/OptionsBoard';

export default {
  components:{     
    [Screens.STARTSCREEN]: StartScreen,
    [Screens.GAMEBOARD]: GameBoard,
    [Screens.AUCTIONBOARD]: AuctionBoard,
    [Screens.CARDOPTIONSBOARD]: CardOptionsBoard,
    [Screens.SWAPBOARD]: SwapBoard,
    [Screens.OPTIONSBOARD]: OptionsBoard
  },
  mounted(){
    this.loadOptionsFromStorage();
  },
  computed: {
    ...mapState([
      'selectedCard', 
      'swap',
      'currentComponent']),       
  },
  methods:{
    ...mapActions(['loadOptionsFromStorage']),
  }
}
</script>

<style lang="scss"> 
  .change-screen-enter-active,
  .change-screen-leave-active {
      transition: all .2s
  }

  .change-screen-enter{
    opacity: 0.5;
    transform: translateX(260px);
  }
  .change-screen-leave-active {
      opacity: 0.5;
      transform: translateX(-260px);
  } 
  .hr{
    max-width: 310px;
    width: 100%;
    height: 6px;
    min-height: 6px;
    background: url('./assets/img/text.png');
    background-size: auto 6px;
    margin: 10px auto;
  }  
  body{
    margin: 0;
    padding: 0;
    background-color: #d9ba6f;
    color: #300300;
    background: Url('./assets/img/1580653594_3-p-foni-s-teksturoi-pergamentnogo-lista-9.jpg');
    font-family: 'HARRY';
    background-size: auto;
  }
  section{
    height: 100vh;
  }     
  ul{
    list-style-type: none;
    padding: 0;
  }
  button{
    font-weight: 700;
    border: none;
    background: none;
    text-decoration: underline;
    color: #300;
    &:disabled{
      color: #300300be;
    }
  }
  a:active{   
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  $colors: (
    1: #8819c9,
    2: #10a2b6,
    3: #ce1e67,
    4: #c76413,
    5: #dd0000,
    6: #ffee00,
    7: #1eb400,
    8: #2f0ee9,
    100: #723939,
    200: #4d5f88,
    1000: #467050
  );

  @each $n, $color in $colors {
    .color#{$n}{
      @if $n==6 {color: #300300;}
      @if $n!=6 {color: white;}
      background: $color;
      //border: 1px solid #300;
      cursor: pointer;     
    }
  }

  @font-face {
    // название шрифта(чтобы не запутаться лучше указать название папки со шрифтом) 
    font-family: "HARRY"; 
    // адрес шрифта и тип 
    src: url("./assets/fonts/10467.ttf") format("truetype"); 
    font-style: normal; 
    font-weight: normal; 
  } 

</style>
