<template lang="pug">
  .board(v-touch.tap="saveGameHandle")   
    div.dots
      span(v-for="card in game.board" 
      :key="card.index" 
      class="board-item" 
      :class="cardClass(card)")    
</template>

<script>
  //canvas#upPlayers(ref="upPlayers" height="10px")
  //canvas#downPlayers(ref="downPlayers" height="10px")
  import { mapState, mapGetters, mapActions } from 'vuex'
  export default {
    name: 'Board',
    data(){
      return {
        // arrows: [10,18,25,32,39,46,53,60,67,74,82,90,97,104,111,118,125,132,139,146,154,162,169,176,183,190,197,204,211,218,226,234,241,248,255,262,269,276,283,290],
      }
    },
    computed: {
      ...mapState(['game']),
      ...mapGetters(['currentPlayer']),
    },
    methods:{
      ...mapActions(['saveGame']),
      saveGameHandle(evt){        
        this.saveGame();
      },
      getOwnerCard(id){
        const player = this.game._findPlayer(id);
        return player ? player.index : null;
      },
      cardClass(card){
        return [ 
          card.cardType, 
          { ['player'+ this.getOwnerCard(card.owner)] : this.getOwnerCard(card.owner)!==null }, 
          { 'active': this.currentPlayer.position === card.index } 
        ];
      }
    }
  }
</script>

<style lang="scss" scoped>
 @mixin circle5px {
    width: 6px;
    height: 6px;
  }
  .board{
    padding: 0;
    margin: 0;
  }
  canvas {
    padding: 0;
    margin: 0;
  }
  #upPlayers{
    position: relative;
    top: 9px;
  }
  #downPlayers{
    position: relative;
    top:-10px;
  }
  .dots{
    padding: 0;
    margin: 8px 0 0 0;
  }
  .board-item{    
    display: inline-block;
    margin: 0 0.5px;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    border: solid 1px #530505;
    vertical-align:middle;
    @each $number, $color in (0, #ff4848), (1, #3939ff), (2, #92dd92), (3, #ffa500) {
      &.player#{$number} {
        background: $color;
        border-color: $color;
      }
    }  
    &.start,
    &.jail,
    &.free,
    &.tojail{
      @include circle5px();
      background-color: #530505;
    }
    &.tax, 
    &.magic, 
    &.potion {
      background-color: grey;
    }
    &.active::after{
      content: ' ';
      position: relative;
      display: block;
      top: -10px;
      left: 1px;
      width: 2px;
      height: 6px;
      background: #530505;
    }
  }
  
</style>