<template lang="pug">
  transition
    .new-card(v-touch:tap="doAction" :class="backgroundCardClass")
      .card-body
        span.card-index(:class="fontColorClass") {{ card.index }}           
        div(v-if="card.cardType===CardTypes.NORMAL")  
          .normal-card-owner {{ getCardOwner(card.owner) }}       
          .normal-card-category {{ card.category }} {{ card.fullRent ? "*" : "" }}
          .normal-card-name {{ card.name }}
          .normal-card-price(v-if="!moreInfo") Цена: {{ card.cost }}
          .normal-card-rent Рента: {{ rent }}
          .normal-card-rent(v-if="moreInfo && !card.disabled") Стоимость залога: {{ card.deposit }}
          .normal-card-rent(v-if="moreInfo && card.disabled") Стоимость выкупа: {{ card.buyout }}
        div(v-else-if="(card.cardType===CardTypes.MAGIC || card.cardType===CardTypes.POTION) && !card.subType")
          .magic-card-name {{ card.name }}
        div(v-else-if="card.subType || card.cardType===CardTypes.START || card.cardType===CardTypes.FREE || card.cardType===CardTypes.TOJAIL")
          .special-card-name {{ card.name }}
          .special-card-disc {{ card.disc }}
        div(v-else-if="card.cardType===CardTypes.TAX")
          .normal-card-name {{ card.name }}
          .normal-card-price Сумма: {{ card.rent[0] }}
        div(v-else-if="card.cardType===CardTypes.TRANSPORT || card.cardType===CardTypes.FACTORY")
          .normal-card-owner {{ getCardOwner(card.owner) }}  
          .normal-card-name {{ card.name }}
          .normal-card-disc {{ card.disc }}
          .normal-card-price(v-if="!moreInfo") Цена: {{ card.cost }}
          .normal-card-rent(v-if="moreInfo && !card.disabled") Стоимость залога: {{ card.deposit }}
          .normal-card-rent(v-if="moreInfo && card.disabled") Стоимость выкупа: {{ card.buyout }}
        div(v-else-if="card.cardType===CardTypes.JAIL")
          .special-card-name {{ card.name }}
          .special-card-disc Узники Азкабана:
          ul
            li(v-for="player in jail" :key="player.id") {{ player.name }}
        img.sold(v-if="card.disabled" src="../assets/img/sold.png")
      .preload    
</template>
<script>
import { CardTypes } from '../monopoly/types/m-board-types';
import { mapState, mapActions } from 'vuex';
export default {
  name: 'Card',
  props: {
    card :{
      type: Object,
      required: true,
    },
    action: Boolean,
    moreInfo: Boolean,
  },
  data(){
    return {
      CardTypes,
      preloadArray:[
        "../assets/img/card1.png",
        "../assets/img/card2.png",
        "../assets/img/card3.png",
        "../assets/img/card4.png",
        "../assets/img/card5.png",
        "../assets/img/card6.png",
        "../assets/img/card7.png",
        "../assets/img/card8.png",
        "../assets/img/card9.png",
        "../assets/img/cardmagic.png",
      ]
    }
  },
  methods: {     
    getCardOwner(ownerID){
      const owner = this.game._findPlayer(ownerID) || null;
      return owner ? owner.name : 'Владельца нет';
    },
    doAction(){
      if(!this.action){
        return;
      }
      if(this.statusAction){
        this.game.doNext();
      } else if(!this.statusGo) {
        this.game.endTurn();
      } else {
        this.game.go();
      }
    },    
  },
  computed: {
    ...mapState({
      game: 'game',
      statusAction: (state) => state.game._action,
      statusGo: (state) => state.game._endOfTurn,
      jail: (state) => state.game.jail,
    }),
    fontColorClass(){
      const {catIndex} = this.card;
      if(catIndex===6){
        return ['text-color-black'];
      }
    },
    backgroundCardClass(){
      const {catIndex, cardType} = this.card;
      if(catIndex>0&&catIndex<10){
        return ['bg-'+catIndex]
      } else {
        if(cardType === CardTypes.MAGIC || cardType === CardTypes.POTION) {
          return ['bg-magic'];
        }
        return ['bg-9'];
      }
    }, 
    rent(){
      if(this.card.houses===0 && this.card.fullRent){
        return this.card.rent[0] * 2;
      } else {
        return this.card.rent[this.card.houses];
      }
    }
  },
}
</script>
<style lang="scss" scoped>
  @each $n in (1,2,3,4,5,6,7,8,9,'magic') {
    .bg-#{$n}{
      background: url('../assets/img/card'+$n+'.png');
      transition: 0.3s;
    }
  }  
  .preload{
    display: none;
  }
  .text-color-black{
    color: black !important;
  }
  .new-card{
    text-align: left;       
    width: 220px;
    height: 343px;
    margin: 0 auto; 
    font-size: 16pt;
    .card-body{
      width: (220px - 18px - 18px);
      height: (343px - 25px - 18px);
      padding: 25px 18px 18px 18px; 
      .sold{
        position: relative;
        left: -17px;
      }
      ul{
        margin: 10px 0;
        text-align: center;
        font-size: 14pt;
      }
      .special-card-name{
        text-align: center;
        padding: 0 3px;
        font-size: 18pt;
      }
      .special-card-disc{
        text-align: center;
        margin-top: 10px;
        padding: 0 3px;
        font-size: 14pt;
      }
      .magic-card-name{
        font-size: 28pt;
        text-align: center;
        margin: 55px 0 0 0;
      }     
      .normal-card-disc{
        font-size: 12pt;
        margin: 10px 0 10px 10px;
      }
      .normal-card-category{
        margin: -20px 0 0 10px;
        font-size: 20pt;
        text-decoration: underline;
      }
      .normal-card-name{
        margin: 10px 0 0 10px;        
      }
      .normal-card-price{
        margin: 0 0 0 10px;
        font-size: 14pt;
      }
      .normal-card-rent{
        margin: 0 0 0 10px;
        font-size: 14pt;
      }
      .normal-card-owner{
        position: relative;
        color: white;
        bottom: -270px;
        right: -8px;
        text-align: right;
      }
    }   
    .card-index{
      position: relative;
      display: block;
      top: -10px;
      left: 150px;
      color: white;
      font-size: 12pt;
      width: 24px;
      text-align: center;
    }
  }
</style>