import board from './m-board';
import magicCards from './m-magic-cards';
import potionCards from './m-potion-cards';
import { CardTypes, MagicAndPotionTypes } from './types/m-board-types';
import Player from './types/m-player-types';
import Auction from './m-auction';

class MonopolyGame {
  constructor(settings = {}){
    this.maxPlayer = settings.maxPlayer || 2;
    this.saleOnGameOver = settings.saleOnGameOver || true;
    this.consoleLog = settings.consoleLog || false;
    this.players = [];
    this.board = board.slice();
    this.whoGo = null; 
    this.finish = false;
    this.double = 0;
    this.jail = [];
    this._endOfTurn = true;
    this._magicCards = magicCards;
    this._potionCards = potionCards;
    this._action = false;
    this.log = ["Необходимо добавить игроков!"];
    this.auction = null;
    this.cubes = {};
    this.currentSpecialCard = null;
    this.specialAction = false;
    this.winner = null;
    this.attention = "";
    this.auctionIsEnd = true;
  }

  /**
   * TODO
   *  необходимо сделать проверку, могут ли заплатить игроки, хватает ли у них денег (на волшебнгой карте дарят игроку деньги)
   *  
   * 
   */


  // Возвращает игрока по ID
  _findPlayer(id){
    return this.players.filter(plr=>plr.id===id)[0];
  }

  // Возвращает индекс игрока в массиве по ID
  _findPlayerIndex(id){
    return this.players.findIndex(plr=>plr.id === id);
  }

  //Сохраняет Лог игры
  _log(){
    for(let i=0; i < arguments.length; i++ ){
      this.log.push(arguments[i]);
      if(this.consoleLog) {
        console.log(arguments[i]); 
      }           
    }
  }
  
  // Возвращает ID следующего игрока в массиве
  _nextPlayer(id){
    const maxIndex = this.players.length;
    const fID = this._findPlayerIndex(id);
    let player = null;
    if((fID + 1) >= maxIndex){
      player = this.players[0].id;
    } else {
      player = this.players[fID+1].id;
    }    
    if(this._findPlayer(player).gameOver){
      player = this._nextPlayer(player);
    }
    return player;
  }

  // Меняет позицию в свойствах игрока
  _changePosition(id, pos){
    const fID = this._findPlayerIndex(id);
    const oldPos = this.players[fID].position;
    this.currentSpecialCard = null;
    this.players[fID].position = pos;
    switch (this.board[pos].cardType) {
      case CardTypes.JAIL:
      case CardTypes.FREE:
      case CardTypes.START:
        this._action = false;        
        break;    
      default:
        this._action = true;
        break;
    }   
    this._log(`Позиция игрока ${this.players[fID].name} сменилась с ${oldPos} на ${pos}`);
  }

  // Вычисляет новую позицию игрока
  _move(id, cubeSumm){
    const playerKey = this._findPlayerIndex(id);
    const max = this.board.length;
    const summ = this.players[playerKey].position + cubeSumm;
    if(summ < max){
      this._changePosition(id, summ);
    } else {
      this._changePosition(id, 0 + (summ-max));
      this.players[playerKey].cash += 200;
      this._log("Круг пройден. Игрок "+ this.players[playerKey].name + " получает 200М");
    }
  }

  // Отправляет в тюрьму по ID игрока
  _moveToJail(id){
    const player = this._findPlayer(id);
    if(player.jailFreeCards.length>0){
      this.players[player.index].jailFreeCards.pop();
      this._log("Игрок "+ player.name +" тратит карту выхода из тюрьмы!")
    } else {
      this._changePosition(id, this._getJailPosition(),);
      this.jail.push({id: id, name: player.name, tryes: 0});
      this._log("Игрок "+ player.name +" отправляется в тюрьму!")      
    } 
    this._switchPlayer();
  }

  // Освобождает от тюрьмы
  _jailBreak(id){
    const player = this._findPlayer(id);
    this.jail = this.jail.filter(plr=>plr.id!==id);
    this.double = 0;
    this._log(player.name + " вы выходите из тюрьмы!");
  }

  // Возвращает позицию поля "Тюрма"
  _getJailPosition(){
    return this.board.filter(card=>card.cardType===CardTypes.JAIL)[0].index;
  }

  // Проверяет по ID находится ли игрок в тюрьме, и сколько он сделал бросков кубика. Освобождает его, если больше 3-х.
  _jailCheck(id){
    const player  = this._findPlayer(id);
    const intoJail = this.jail.findIndex(plr=>plr.id===id);
    let result = intoJail != -1;
    if(result){
      if(this.jail[intoJail].tryes < 3){
        this._log(player.name + " вы походили всего "+ this.jail[intoJail].tryes +" из 3-х вы остаетесь в тюрьме!");      
      } else {
        this.jail = this.jail.filter(plr=>plr.id!==id); 
        result = false; 
        this._log(player.name + " вы выходите из тюрьмы!");     
      }
    }
    return { jailStatus: result, jailNumber: intoJail}   
  }

  // Возвращает по ID игрока текущую карту, на которой стоит игрок
  _getCurentCard(id){
    const playerIndex = this._findPlayerIndex(id);
    const position = this.players[playerIndex].position;
    return this.board[position];
  }

  // Определяет платежеспособность игрока по ID
  _solvency(id, summ) {
    const player = this._findPlayer(id);
    return player.cash >= summ;
  }

  // Вычисляет уплату ренты другому игроку
  _payRent(id, owner=null, summ=0){
    const playerKey = this._findPlayerIndex(id);
    if(!this._solvency(id, summ)){
      this._log(`${this.players[playerKey].name } Вы не можете заплатить ренту`);
      return false;
    }
    if(owner){
      const ownerKey = this._findPlayerIndex(owner);      
      this.players[ownerKey].cash += summ;
      this.players[playerKey].cash -= summ;
      this._log(`Заплачена рента ${summ} игроку ${this.players[ownerKey].name} от игрока ${this.players[playerKey].name}`);
    } else {
      this.players[playerKey].cash -= summ;
      this._log(`Заплачена рента ${summ} игроком ${this.players[playerKey].name}`);
    }
    return true;
    //this._log("Деньги игрока " + this.players[playerKey].name + ": " + this.players[playerKey].cash);
  }

  // Оплата тюремного штрафа
  payFine(){
    const player = this._findPlayer(this.whoGo);
    const intoJail = this.jail.findIndex(plr=>plr.id===player.id);
    if(!this._solvency(player.id, 50)){
      this._log(`${player.name} у вас нехватает денег для выкупа!`);
      return false;
    }
    if(intoJail!=-1){
      this.players[player.index].cash -= 50;
      this._jailBreak(player.id);
    } else {
      this._log(`${player.name} вы не в тюрьме!`);
    }
  }

  // Покупка собственности
  buyProperty(){
    const player = this._findPlayer(this.whoGo);
    const card = this.board[player.position];
    if(card.canBuy && !card.owner){
      this._action = !this._buyCard(player.id, card.index);
    }
  }

  playerCards(playerID){
    const player = this._findPlayer(playerID);    
    const cards = this.board.filter(val=>val.owner===player.id);
    const jailCards = player.jailFreeCards.map(val=>{
      return Object.assign({},val,{catIndex: 1000});
    })
    const allPlayerCards = cards.concat(jailCards);
    allPlayerCards.sort((a,b)=>{
      if(a.catIndex<b.catIndex){
        return -1;
      } else if(a.catIndex>b.catIndex){
        return 1;
      } else {
        if(a.index<b.index){
          return -1;
        } else {
          return 1;
        }
      }
    });
    return allPlayerCards;
  }

  // Отказ от покупки, запуск аукциона
  cancelBuy(){
    const player = this._findPlayer(this.whoGo);
    const card = this.board[player.position];
    if(card.canBuy && !card.owner && this._action){
      this.auction = new Auction(
        card, 
        this.players.filter(plr=>plr.id!==player.id&&!plr.gameOver),
        (playerIndex, cardIndex, bet) => {
          if(playerIndex!=null && cardIndex) {
            //console.log(this.players[playerIndex], playerIndex)
            this.players[playerIndex].cash -= bet;
            this.board[cardIndex].owner = this.players[playerIndex].id;
            this.board[cardIndex].color = 'player' + playerIndex;
            this._checkPurchasedAllCategory(this.board[cardIndex].catIndex, this.players[playerIndex].id);
          }          
          this._action = false;
        });
    }
  }

  // Производит покупку карты, и увеличивает ренту, если купленны все карты одной категории.
  _buyCard(id, cardIndex){
    const playerKey = this._findPlayerIndex(id);
    if(!this._solvency(id, this.board[cardIndex].cost)){
      this._log(`${this.players[playerKey].name } Нехватает денег на покупку!`);
      return false;
    }
    this.players[playerKey].cash -= this.board[cardIndex].cost;
    this.board[cardIndex].owner = this.players[playerKey].id;
    this.board[cardIndex].color = 'player' + playerKey;
    this._checkPurchasedAllCategory(this.board[cardIndex].catIndex, id);    
    this._log(`Куплена собственность ${this.board[cardIndex].name} игроком ${this.players[playerKey].name}`);
    return true;
    //this._log("Деньги игрока " + this.players[playerKey].name + ": " + this.players[playerKey].cash);
  }

  // Купить дом на карту
  byeHouse(card){
    const player = this._findPlayer(this.whoGo);
    if(!this._solvency(player.id, card.buildCost)){
      this._log(`${player.name } Нехватает денег на постройку!`);
      return;
    }
    if(!card.fullRent){
      this._log(`${player.name } необходимо купить всю собственность в регионе ${card.category}!`);
      return;
    }
    if(card.cardType===CardTypes.NORMAL && card.houses < 5 && !card.disabled){
      this.board[card.index].houses += 1;
      this.players[player.index].cash -= card.buildCost;
    }    
  }

  // Продать дом на карте
  saleHouse(card){
    const player = this._findPlayer(this.whoGo);
    if(card.cardType===CardTypes.NORMAL && card.houses > 0){
      this.board[card.index].houses -= 1;
      this.players[player.index].cash += card.buildCost/2;
    }    
  }

  // Проверяет, купленны ли все карты одной категории, и увеличивает ренту
  _checkPurchasedAllCategory(catIndex, playerID){
    const allCategoryCards = this.board.filter(card=>card.catIndex === catIndex);
    let oneOwner = !allCategoryCards.some(card=>{
      return card.owner !== playerID;
    });
    if(oneOwner) {
      for(let i=0; i<allCategoryCards.length; i++){
        this.board[allCategoryCards[i].index].fullRent = true;
      }
    } else {
      for(let i=0; i<allCategoryCards.length; i++){
        this.board[allCategoryCards[i].index].fullRent = false;
      }
    }
  }

  // Меняет игрока, который ходит
  _switchPlayer(needOfSwap=true){
    this.double = 0;
    this._endOfTurn = true;
    this._action = false;    
    this.currentSpecialCard = null;
    this.specialAction = false;
    if(needOfSwap){
      this.whoGo = this._nextPlayer(this.whoGo);
    }    
  }  

  // Создает Игрока
  createPlayer(name){
    if(this.players.length >= this.maxPlayer) {
      return;
    }
    const player = new Player(name);
    this.players.push(player);
    const playerIndex = this._findPlayerIndex(player.id);
    this.players[playerIndex].index = playerIndex;
    if(!this.whoGo){
      this.whoGo = player.id;
    }
    this._log(`В игру добавлен игрок ${player.name}`, 'Можно ходить!');    
  }

  // Начало хода
  go(){
    if(!this.whoGo) {
      this._log("Добавте игроков!");
      return;
    }
    if(this._action) {
      this._log("Сначало выполните действие!");
      return;
    }
    if(!this._endOfTurn){
      this._log("Вы не можете сейчас походить!");
      return;
    }    
    this._endOfTurn = false;
    const player = this._findPlayer(this.whoGo);
    const { jailStatus, jailNumber } = this._jailCheck(player.id);
    const cube1 = Math.floor((Math.random() * 6)+1); 
    const cube2 = Math.floor((Math.random() * 6)+1);
    if(cube1 !== cube2) {      
      this.double = 0;
      if(jailStatus){
        this.jail[jailNumber].tryes += 1;
        this._switchPlayer();
      } else {
        this._move(player.id, cube1+cube2);
      }
    } else if( cube1 === cube2 && this.double < 2) {
      this.double += 1;
      if(jailStatus){ this._jailBreak(player.id); }
      this._move(player.id, cube1+cube2);
    } else {
      this._moveToJail(player.id);
    }
    this.cubes = { player, cube1, cube2 };
    this._log("У игрока "+ player.name +" выпало "+ cube1 +" и " + cube2 +" Позиция:" + player.position + " Деньги:" + player.cash); 
  }

  // Выполняет действие на карте
  doNext(){
    if(!this._action){
      this._log("Вам пока нечего делать, завершите ход!");
      return;
    }
    const player = this._findPlayer(this.whoGo);
    const card = this._getCurentCard(player.id);    
    this._action = !this._doAction(player, card, true);
  }

  //Обмен картами {player, cards, cash}
  swapCard(saler, customer){
    if(saler.cash > 0) {
      this._changeCashFromPlayers(saler.cash, saler.player, customer.player);      
    }    
    if(customer.cash > 0){
      this._changeCashFromPlayers(customer.cash, customer.player, saler.player);
    }
    if(saler.cards) {
      this._changeCardsOwner(saler.cards, saler.player, customer.player);            
    }
    if(customer.cards){
      this._changeCardsOwner(customer.cards, customer.player, saler.player);
    }
  }

  _changeCashFromPlayers(summ, fromPlayer, toPlayer){
    if(this._solvency(fromPlayer.id, summ)){
      this.players[fromPlayer.index].cash -= +summ;
      this.players[toPlayer.index].cash += +summ;
    } else {
      this._log('Нехватает денег у '+ fromPlayer.name + ' для обмена');
    }
  }

  _changeCardsOwner(cards, fromPlayer, toPlayer){
    cards.forEach(card=>{
      if(card.owner===fromPlayer.id){
        this.board[card.index].owner = toPlayer.id;
        this._checkPurchasedAllCategory(card.catIndex, toPlayer.id);
      }
    })
  }

  // Закладываем имущество
  toLay(cardIndex){
    const player = this._findPlayer(this.whoGo);
    const card = this.board[cardIndex];
    if(card.owner !== player.id){
      this._log('Это не ваша карта!');
      return;
    } else {
      if(card.disabled){
        this._log('Вы уже заложили эту собственность!');
      } else {
        if(card.houses > 0){
          this.board[card.index].houses -= 1;
          this.players[player.index].cash += card.buildCost/2;
        } else {
          this.board[card.index].disabled = true;
          this.players[player.index].cash += card.deposit;
          this._checkPurchasedAllCategory(card.catIndex, player.id);
        }
      }
    }
  }

  // Выкупаем имущество
  redeem(cardIndex){
    const player = this._findPlayer(this.whoGo);
    const card = this.board[cardIndex];
    if(card.owner !== player.id){
      this._log('Это не ваша карта!');
      return;
    } else {
      if(card.disabled){
        this.board[card.index].disabled = false;
        this.players[player.index].cash -= card.buyout;
        this._checkPurchasedAllCategory(card.catIndex, player.id);
      } else {
        this._log('Эту карту выкупать не надо!');
      }
    }
  }

  // Проверяем карту и делаем действия на ней
  _doAction(player, card, buy){
    let actionIsComlpite = true;
    if(this.specialAction){
      const [ rerun , complite ] = this._doSpecialAction(player, this.currentSpecialCard);
      if(rerun){
        return false;
      }
      return complite;
    }
    switch (card.cardType) {
      case CardTypes.NORMAL:
          if(card.owner){
            if(card.owner !== player.id && !card.disabled){           
              let summ = card.rent[card.houses];
              if(card.fullRent && card.houses === 0){
                summ *= 2;
              }
              actionIsComlpite = this._payRent(player.id, card.owner, summ);
            }
          } else {
            if(buy){
              actionIsComlpite = this._buyCard(player.id, card.index);
            }
          }     
        break;
      case CardTypes.TOJAIL:
        this._moveToJail(player.id);
        this._log(`${player.name} отправляется в тюрьму`);
        break;
      case CardTypes.TAX:
        actionIsComlpite = this._payRent(player.id, null, card.rent[0]);
        break;
      case CardTypes.TRANSPORT:
        if(card.owner){
          if(card.owner !== player.id && !card.disabled){
            const transportCount = this.board.filter(c=>c.cardType===CardTypes.TRANSPORT&&c.owner===card.owner).length;
            const summ = card.rent[transportCount-1];
            actionIsComlpite = this._payRent(player.id, card.owner, summ);
          }
        } else {
          if(buy){
            actionIsComlpite = this._buyCard(player.id, card.index);
          }
        }
        break;
      case CardTypes.FACTORY:
        if(card.owner){
          if(card.owner !== player.id && !card.disabled){
            const factoryCount = this.board.filter(c=>c.cardType===CardTypes.FACTORY&&c.owner===card.owner).length;
            let cubeTry = factoryCount > 1 ? 10 : 4;
            let summ = 0;
            this._log('Считаем ренту');
            for(let i = 0; i < cubeTry; i ++){
              const cube1 = Math.floor((Math.random() * 6)+1); 
              summ += cube1;
              this._log(`Выпало ${cube1} сумма ${summ}`);
            }
            actionIsComlpite = this._payRent(player.id, card.owner, summ);
          }
        } else {
          if(buy){
            actionIsComlpite = this._buyCard(player.id, card.index);
          }
        }
        break;
      case CardTypes.MAGIC:
        {
          const randomIndex = Math.floor(Math.random() * this._magicCards.length);          
          this.currentSpecialCard = this._magicCards[randomIndex];
          this.specialAction = true;
          actionIsComlpite = false;          
        }        
        break;
      case CardTypes.POTION:
        {
          const randomIndex = Math.floor(Math.random() * this._potionCards.length);
          this.currentSpecialCard = this._potionCards[randomIndex];
          this.specialAction = true;
          actionIsComlpite = false;          
        }        
        break;
      default:
        break;
    }
    return actionIsComlpite;
  }

  // Выполняем дествия магических карт
  _doSpecialAction(player, card){
    //this.currentSpecialCard = card;
    this.specialAction = false;
    let actionIsComlpite = true;
    let rerun = false;
    switch (card.subType) {
      case MagicAndPotionTypes.GIFT:        
        this.players[player.index].cash += card.cost;                     
        break;
      case MagicAndPotionTypes.GO:
        this.players[player.index].position = 0;
        this.players[player.index].cash += 200;        
        break;
      case MagicAndPotionTypes.GOTO:
        if(card.destanation > 0){
          if(card.destanation < player.position || card.importantPay){
            this.players[player.index].cash += card.cost;
          }
          this._changePosition(player.id, card.destanation);            
        } else {
          this._changePosition(player.id, player.position + card.destanation);
        }
        actionIsComlpite = false;  
        rerun = true;
        break;
      case MagicAndPotionTypes.HOUSETAX:
        {          
          const allPlayersCards = this.board.filter(crd=>crd.owner===player.id&&crd.cardType===CardTypes.NORMAL);
          let summ = allPlayersCards.reduce((acc=0, val)=>{
            const s = (val.houses < 5) ? (val.houses * card.cost) : (card.cost * 3)
            return acc + s;
          }, 0);
          if(!this._solvency(player.id, summ)){
            this._log(`Нехватает денег на оплату!`);
            this.specialAction = true;
            actionIsComlpite = false;
          } else {
            this.players[player.index].cash -= summ;
          }            
        }        
        break;
      case MagicAndPotionTypes.JAILFREE:
        this.players[player.index].jailFreeCards.push(card);        
        break;
      case MagicAndPotionTypes.PLAYERSGIFT:
        {
          const otherPlayers = this.players.filter(plr=>plr.id!==player.id&&!plr.gameOver);
          let summ = otherPlayers.reduce((acc, val)=>{
            this.players[val.index].cash -= card.cost;
            return acc + card.cost;
          }, 0);
          this.players[player.index].cash += summ;
        }
        break;
      case MagicAndPotionTypes.PLAYERSTAX:
        {
          const otherPlayers = this.players.filter(plr=>plr.id!==player.id&&!plr.gameOver);
          let summ = otherPlayers.length * card.cost;
          if(!this._solvency(player.id, summ)){
            this._log(`Нехватает денег на оплату!`);
            this.attention = `${player.name} нехватает ${summ-player.cash}М!`
            this.specialAction = true;
            actionIsComlpite = false;
          } else {
            otherPlayers.forEach(plr=>{
              this.players[plr.index].cash += card.cost;
              this.players[player.index].cash -= card.cost;
            })
          }
        }
        break;
      case MagicAndPotionTypes.TAX:
        if(!this._solvency(player.id, card.cost)){
          this._log(`Нехватает денег на оплату!`);
          this.attention = `${player.name} нехватает ${card.cost-player.cash}М!`
          this.specialAction = true;
          actionIsComlpite = false;
        } else {
          this.players[player.index].cash -= card.cost;
        }        
        break;
      case MagicAndPotionTypes.TOJAIL:
        this._moveToJail(player.id);        
        break;
      default:
        break;
    }
    this._log(card.name + " " + card.disc);
    return [rerun, actionIsComlpite];
  }

  gameOver(id){
    const player = this._findPlayer(id);
    this.players[player.index].gameOver = true;
    this.board.forEach(card=>{
      if(card.owner === player.id){
        card.disabled = true;
        card.houses = 0;
        card.fullRent = false;
        if(this.saleOnGameOver){
          card.owner = null;
        }
      }
    })
    this._checkWinner();
    this._switchPlayer();
  }

  _checkWinner(){
    const activePlayers = this.players.filter(plr=>!plr.gameOver);
    console.log(activePlayers.length === 1);
    if(activePlayers.length === 1){      
      this.winner = activePlayers[0];   
      this.finish = true;
    }
  }

  clearGameData(){
    this.board.forEach(card=>{
      if(card.houses){
        card.houses = 0;
      }
      this.fullRent = false;
      if(card.owner && card.owner !== 'game'){
        card.owner = null;
      }
      card.disabled = false;
    })
    this.jail = [];
    this.players = [];
    this.log = ["Необходимо добавить игроков!"];
  }

  getPlayerCards(player){
    return this.board.filter(card=>card.owner===player.id);
  }

  // Заканчиваем ход
  endTurn(){
    if(this._endOfTurn){
      this._log("Вам нужно походить, или ваш ход уже закончен!");
      this.attention = "Ход уже закончен";
      return;
    }    
    if(this._action){
      this._log("Выполните все действия!");
      this.attention = "Выполните действия";
      return;
    }  
    if(this.double === 0){
      this.whoGo = this._nextPlayer(this.whoGo);
    }       
    this.currentSpecialCard = null;
    this._endOfTurn = true;
  }  
}

export default MonopolyGame