<template lang="pug">
  section.card-options
    .hr
    new-card(:card="selectedCard", :more-info="true")
    .hr
    div {{ currentPlayer.name }} / Деньги: {{ currentPlayer.cash }}
    .hr
    a.link(href="#" @click.prevent="toLay(selectedCard.index)" v-if="!selectedCard.disabled") Заложить имущество
    a.link(href="#" @click="redeem(selectedCard.index)" v-else) Выкупить Имущество
    a.link(href="#" @click="byeHouse(selectedCard)" v-if="selectedCard.fullRent" ) Построить дом / отель / {{ selectedCard.buildCost }}М
    a.link(href="#" @click="saleHouse(selectedCard)" v-if="selectedCard.houses > 0" ) Продать дом / {{ selectedCard.buildCost / 2 }}М
    a.link(href="#" @click.prevent="clearSelectedCard") Выход
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from 'vuex';
import NewCard from './NewCard';
export default {
  name: 'CardOptionsBoard',
  components: {
    'new-card': NewCard,
  },
  computed: {
    ...mapState(['selectedCard']),
    ...mapGetters(['currentPlayer']),
  },
  methods: {
    ...mapMutations(['clearSelectedCard']),
    ...mapActions(['toLay', 'redeem', 'byeHouse', 'saleHouse']),
  },
}
</script>
<style lang="scss" scoped>
  .card-options{
    //padding-top: 20px;
    text-align: center;
    font-size: 16pt;
    a{
      text-decoration: none;
    }
    .link{
      display: block;
    }
  }
</style>