class Auction{
  constructor(card, players, cb){    
    this.lot = card;
    this.players = players;
    this.bet = card.cost;
    this.whoBet = null;
    this.current = players[0];
    this._cb = cb; 
    this.isEnd = false;
  }
  accept(bet){
    if(this.current.cash >= bet && this.bet < bet){
      this.bet = bet;
      this.whoBet = this.current;      
      this.next();
    }
  }
  cancel(){
    this.players = this.players.filter(plr=>plr.id!==this.current.id);     
    this.next();    
  }
  next(){ 
    this.checkWinner();   
    let index =  this.players.findIndex(p=>p.id===this.current.id) + 1;
    if(index >= this.players.length) {
      index = 0;
    }
    this.current = this.players[index];
  }
  checkWinner(){
    if(this.players.length <= 1 && this.whoBet){
      this.isEnd = true;
      this._cb(this.whoBet.index, this.lot.index, this.bet);      
    } else if(this.players.length == 0) {
      this.isEnd = true;
      this._cb(null,null,0);      
    }
  }
}

export default Auction;