<template lang="pug">
  section.game-board
    new-card(:card="currentCard")
    .hr
    .caption АУКЦИОН
    .hr
    player-cards(:allNoEdit="true" :auction="true")
    div Цена лота: {{ game.auction.bet }}
    div Делает ставку: {{game.auction.current.name}} / {{ game.auction.current.cash }}М
    div.line Ставка: {{ bet }}
    div     
      a.line(href="#" @click="inc(10)") +10
      a.line(href="#" @click="inc(50)") +50
      a.line(href="#" @click="accept") V
      a.line(href="#" @click="cancelAuc") X
</template>
<script>
import NewCard from './NewCard';
import PlayerCards from './PlayerCards';
import { mapGetters, mapState, mapActions } from 'vuex';
export default {
  name: 'AuctionBoard',
  data(){
    return {
      bet: 0,
    }
  },
  methods:{
    ...mapActions(['acceptAuc', 'cancelAuc']),
    inc(cost){
      this.bet += cost;
    },
    accept(){
      this.acceptAuc(this.bet);
    }, 
  },
  mounted(){
    this.bet = this.game.auction.bet;
  },
  components:{
    'new-card': NewCard,
    'player-cards': PlayerCards,
  },
  computed: {
    ...mapState(['game']),
    ...mapGetters(['currentCard']),
  },
}
</script>
<style lang="scss" scoped>
  .game-board{
    display: flex;
    text-align: center;
    flex-direction: column;
    font-size: 16pt;
    a{
      text-decoration: none; 
      //color: #300;
    }
    .line{
      display: inline-block;
      margin: 0 5px;
    }
  }
  .caption{
    font-size: 22pt;
  }
</style>