export const CardTypes = {
  NORMAL: 'normal',
  START: 'start',
  JAIL: 'jail',
  TOJAIL: 'tojail',
  FREE: 'free',
  TAX: 'tax',
  MAGIC: 'magic',
  POTION: 'potion',
  TRANSPORT: 'transport',
  FACTORY: 'factory',
}

export const MagicAndPotionTypes = {
  GO: 'go',
  GIFT: 'gift',
  TAX: 'tax',
  JAILFREE: 'free',
  TOJAIL: 'tojail',
  HOUSETAX: 'housetax',
  GOTO: 'goto', 
  PLAYERSGIFT: 'playersgift',
  PLAYERSTAX: 'playerstax',
}

export class Card {
  constructor(index, name, disc, cardType){
    this.index = index || 0;
    this.name = name || "";
    this.disc = disc || "";
    this.cardType = cardType || CardTypes.NORMAL;
  }
}

export class BoardCard extends Card{
  constructor(settings = {}){
    super(settings.index, settings.name, settings.disc, settings.cardType);
    this.category = settings.category || null;
    this.catIndex = settings.catIndex || null;
    this.cost = settings.cost || 0;
    this.rent = settings.rent || [0,0,0,0,0,0];
    this.fullRent = false;
    this.houses = 0;
    this.buildCost = settings.buildCost || 0;
    this.deposit = settings.deposit || 0;
    this.buyout = this.deposit + (this.deposit * 0.01 * 10);    
    this.canBuy = settings.canBuy || false;
    this.owner = settings.owner || null;
    this.disabled = false;
    this.color = null;    
  }
}

export class MagicCard extends Card{
  constructor(name, disc, {subType, cost, destanation, importantPay}={}){
    super(null, name, disc, CardTypes.MAGIC);
    this.subType = subType || MagicAndPotionTypes.GIFT;
    this.cost = cost || 0;
    this.destanation = destanation || 0;
    this.importantPay = importantPay || false;
  }
}

export class PotionCard extends Card{
  constructor(name, disc, {subType, cost, destanation, importantPay}={}){
    super(null, name, disc, CardTypes.POTION);
    this.subType = subType || MagicAndPotionTypes.GIFT;
    this.cost = cost || 0;
    this.destanation = destanation || 0;
    this.importantPay = importantPay || false;
  }
}