<template lang="pug">
  .cards-board(v-if="!drag")
    div(v-for="card in cards" 
      :key="card.index"
      :class="['small-card','color'+card.catIndex, { 'disabled': card.disabled, 'active': actionEnable }]"
      @click="clickCardHandle(card)"
    )
      | {{ card.fullRent ? '*' : '' }}
      | {{ card.name.slice(0, maxLen) }}
      | {{ card.name.length > maxLen ? '... ' : ' ' }}
      | {{ card.deposit }}
      | {{ card.houses>0 && card.houses<5 ? 'Д'+card.houses : '' }}
      | {{ card.houses===5 ? 'c От.' : '' }}
  draggable.cards-board(v-else :list="cards" :group="group" :class="{ 'border-line': border }")
    div(v-for="card in cards" 
      :key="card.index"
      :class="['small-card','color'+card.catIndex, { 'disabled': card.disabled, 'active': actionEnable }]"
      @click="clickCardHandle(card)"
    )
      | {{ card.fullRent ? '*' : '' }}
      | {{ card.name.slice(0, maxLen) }}
      | {{ card.name.length > maxLen ? '... ' : ' ' }}
      | {{ card.deposit }}
      | {{ card.houses>0 && card.houses<5 ? 'Д'+card.houses : '' }}
      | {{ card.houses===5 ? 'c От.' : '' }}
</template>

<script>
import draggable from 'vuedraggable'
export default {
  name: 'CardsList',
  props: [ 'cards', 'actionEnable', 'group', 'border', 'drag' ],
  components: {
    draggable,
  },
  methods: {
    clickCardHandle(card){
      if(this.actionEnable){
        this.$emit('click', card)
      }
    }
  },
  computed: {
    maxLen(){
      let size = 0;
      const {length} = this.cards;
      if(length<7){
        size = 45;
      } else if(length>6 &&length<13){
        size = 13;
      } else if(length > 12 && length<19) {
        size = 5;
      } else if(length > 18){
        size = 5;
      }
      return size;
    }
  },
}
</script>

<style lang="scss" scoped>
  .disabled{
    text-decoration: line-through;
  }
  .active{
    cursor: pointer;
  }
  .small-card{
    box-sizing: border-box; 
    border: 1px solid rgb(105, 16, 16);
    height: 20px; 
    font-size: 12pt;   
  }
  .cards-board{   
    margin: 0 auto;
    width: 300px;
    height: 120px;
    font-size: 12px;
    display: flex;
    flex-flow: column wrap;
    justify-content: left;     
  }
  .border-line{
    border: 1px solid #300;
  }
</style>